// pages/NewsPage/ui/NewsPage.scss

.newsContainer {
  max-width: 600px;
  margin: 0 auto;
  padding: 16px;
}

.newsList {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.newsImage {
  width: 100%;
  max-height: 400px;
}

.newsCard {
  .ant-card-body {
    padding: 0;
  }

  .content {
    padding: 16px;
  }
  .ant-card {
    border: 1px solid #f0f0f0;
    border-radius: 8px;
  }
  .newsTitle {
    font-size: 16px;
    font-weight: 600;
    margin-bottom: 4px;
    color: #333;
  }

  .newsContent {
    font-size: 14px;
    color: #555;
    white-space: pre-line; // если хотим переносы
    margin-bottom: 8px;
  }

  .newsMeta {
    font-size: 12px;
    color: #999;
  }
}
